import { Grid, Typography } from "@mui/material";
import React from "react";
import { generateStyle } from "Utils/Utils";

const styles = (theme) => ({
  dnAppHeader: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  dnUserProfile: {
    position: "relative",
    padding: 2,
    boxSizing: "border-box",
    background: "white",
    backgroundClip: "padding-box",
    border: "2px solid transparent",
    borderRadius: 10,
    height: 48,
    "&>img": {
      width: 40,
      height: 40,
      objectFit: "cover",
      borderRadius: "8px",
    },
    "&::after": {
      content: '""',
      position: "absolute",
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
      zIndex: "-1",
      margin: "-3px",
      borderRadius: "inherit",
      background: "linear-gradient(to right, red, orange)",
      // background: "linear-gradient(131.9deg, #1292F6 5.89%, #FD6F93 71.67%)",
    },
  },
});

export default function AppHeader(props) {
  const { children } = props;
  const classes = generateStyle(styles);

  return (
    <Grid className={classes.dnAppHeader}>
      {children}
      <Grid className={classes.dnUserProfile}>
        <img
          src="https://cdn5.vectorstock.com/i/1000x1000/89/79/funny-avatar-cunning-emoji-flat-vector-27638979.jpg"
          alt="profile"
        />
      </Grid>
    </Grid>
  );
}
