import { grey } from "@mui/material/colors";
import { createTheme } from "@mui/material/styles";

let palette = {
  primary: {
    main: "#DE496E",
    superLight: "#DE496E18",
  },
  primaryLight: {
    main: "#DE496E18",
  },
};
let borderRadius = 4;

const theme = createTheme({
  palette: palette,
  typography: {
    fontFamily: `'Poppins', "sans-serif"`,
  },
  components: {
    MuiFab: {
      defaultProps: {
        color: "primaryLight",
        size: "medium",
        disableRipple: true,
      },
      styleOverrides: {
        root: {
          borderRadius: borderRadius,
          boxShadow: "unset",
        },
      },
    },
    MuiTextField: {
      defaultProps: {
        variant: "outlined",
        noOutline: true,
      },
      variants: [
        {
          props: { noOutline: true, variant: "outlined" },
          style: {
            background: palette.primary.superLight,
            borderRadius: borderRadius,
            "& input::placeholder": {
              color: palette.primary.main,
            },
            "& fieldset": {
              border: "unset",
            },
          },
        },
        {
          props: { noGb: true },
          style: {
            // background: "transparent",
          },
        },
      ],
    },
    shape: {
      borderRadius: borderRadius,
    },
    MuiButton: {
      defaultProps: {
        variant: "contained",
        disableElevation: true,
        disableRipple: true,
      },
      styleOverrides: {
        root: {
          textTransform: "unset",
        },
      },
    },
    MuiTab: {
      styleOverrides: {
        root: {
          color: "black",
        },
      },
    },
    MuiTabs: {
      defaultProps: {
        disableRipple: true,
        disableTouchRipple: true,
      },
      variants: [
        {
          props: {
            box: true,
          },
          style: {
            minHeight: 40,
            "&>div": {
              "&>div": {
                borderRadius: borderRadius,
              },
            },
            "& .MuiTabs-indicator": {
              background: "white",
              height: 40,
              borderRadius: borderRadius,
              top: 3,
            },
            "& .MuiTabs-flexContainer": {
              padding: "2px 3px",
              background: palette.primaryLight.main,
              width: "fit-content",
              "&>button": {
                minHeight: 34,
                zIndex: 1,
                minWidth: "fit-content",
              },
            },
          },
        },
      ],
    },
  },
});

console.log("theme", theme);

export default theme;
