import {
  Container,
  Grid,
  Typography,
  TextField,
  InputAdornment,
  Stack,
  Zoom,
  Fab,
} from "@mui/material";
import React from "react";
import { generateStyle } from "Utils/Utils";
import { ReactComponent as SearchIcon } from "assets/SearchIcon.svg";
import { ReactComponent as PlusIcon } from "assets/PlusIcon.svg";
import { IconGenerator } from "Component/Common";
import clsx from "clsx";
import AppHeader from "Component/Common/AppHeader";

const styles = (theme) => ({
  dnHomeRoot: {
    position: "relative",
    minHeight: "100vh",
  },
  dnGreetingMsg: {
    "&>h6": {
      fontWeight: "bold",
      lineHeight: "24px",
      "&:first-child": {
        color: theme.palette.primary.main,
      },
    },
  },
  dnNoteCard: {
    display: "flex",
    "& .grey": {
      color: theme.palette.grey[500],
    },
    "&>div": {
      "&:first-child": {
        width: 120,
        textAlign: "center",
        "&>p": {
          color: theme.palette.grey[500],
          fontWeight: "bold",
        },
        "&>h5": {
          fontWeight: "bold",
        },
      },
      "&:nth-child(2)": {
        background: theme.palette.primary.superLight,
        width: "100%",
        borderRadius: theme.shape.borderRadius * 2,
        padding: theme.spacing(2),
        "&>p": {
          fontWeight: "bold",
        },
      },
    },
  },
  dnDateContainer: {
    margin: `${theme.spacing(2)} 0px`,
    display: "flex",
    justifyContent: "flex-start",
    "&>div": {
      marginRight: theme.spacing(1),
    },
    "& .selected": {
      background: theme.palette.primary.superLight,
    },
  },
  dnDateBox: {
    cursor: "pointer",
    textAlign: "center",
    width: 50,
    borderRadius: theme.spacing(2),
    padding: "10px 0px",
    color: theme.palette.primary.main,
    "&>p": {
      lineHeight: "20px",
    },
    "&>div": {
      background: theme.palette.primary.main,
      width: 5,
      height: 5,
      borderRadius: "50%",
      margin: "auto",
      marginTop: 5,
    },
  },
  dnCreateFabIcon: {
    position: "absolute !important",
    bottom: theme.spacing(3),
    right: theme.spacing(3),
  },
});

export default function Home() {
  const classes = generateStyle(styles);
  return (
    <Container sx={{ pt: 1 }} className={classes.dnHomeRoot}>
      <AppHeader>
        <Grid className={classes.dnGreetingMsg}>
          <Typography variant="h6">Good Morning,</Typography>
          {
            // <Typography variant="h6">Good Evening,</Typography>
            // <Typography variant="h6">Good Afternoon,</Typography>
          }
          <Typography variant="h6">Praful</Typography>
        </Grid>
      </AppHeader>
      <Grid>
        <TextField
          margin="dense"
          fullWidth
          size="small"
          placeholder="Search"
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <IconGenerator icon={SearchIcon} />
              </InputAdornment>
            ),
          }}
        />
      </Grid>
      <Grid className={classes.dnDateContainer}>
        {Array.from(new Array(6)).map((j, i) => (
          <Grid
            className={clsx(classes.dnDateBox, { ["selected"]: i === 4 })}
            key={i}
          >
            <Typography>{20 + i}</Typography>
            <Typography variant="body2">Su</Typography>
            <div></div>
          </Grid>
        ))}
      </Grid>
      <Grid>
        {Array.from(new Array(3)).map((j, i) => (
          <Grid className={classes.dnNoteCard} mb={2} key={i}>
            <Grid>
              <Typography>Dec, 2019</Typography>
              <Typography variant="h5"> 21</Typography>
            </Grid>
            <Grid>
              <Typography>
                Your story goes here. and can be use this second line as well.
                and also may required to use this third like or other lime
                alos....
              </Typography>
              <Stack
                spacing={2}
                direction="row"
                justifyContent="space-between"
                mt={1}
              >
                <Typography className="grey">01:30pm sun</Typography>
                <Stack spacing={1} direction="row">
                  <Typography>5</Typography>
                  <Typography>1</Typography>
                </Stack>
              </Stack>
            </Grid>
          </Grid>
        ))}
      </Grid>
      <Zoom
        // timeout={transitionDuration}
        // const transitionDuration = {
        //   enter: theme.transitions.duration.enteringScreen,
        //   exit: theme.transitions.duration.leavingScreen,
        // };
        // style={{
        //   transitionDelay: `${
        //     value === index ? transitionDuration.exit : 0
        //   }ms`,
        // }}
        in={true}
        className={classes.dnCreateFabIcon}
        unmountOnExit
        href="/#/create"
      >
        <Fab
        // sx={fab.sx} aria-label={fab.label} color={fab.color}
        >
          <IconGenerator icon={PlusIcon} />
        </Fab>
      </Zoom>
    </Container>
  );
}
