import React, { useState } from "react";
import {
  Container,
  Fab,
  Grid,
  Tab,
  Tabs,
  TextField,
  Typography,
} from "@mui/material";
import AppHeader from "Component/Common/AppHeader";
import { ReactComponent as BackIcon } from "assets/BackIcon.svg";
import { IconGenerator } from "Component/Common";
import moment from "moment";
import { generateStyle } from "Utils/Utils";

const styles = (theme) => ({
  dnStoryInput: {
    marginTop: theme.spacing(2),
    "& textarea": {
      height: "calc(100vh - 170px) !important",
    },
  },
});

export default function CreateNote() {
  const classes = generateStyle(styles);

  const [tabIndex, setTabIndex] = useState(0);

  const handleTabChange = (e, newIndex) => {
    console.log("newIndex", newIndex);
    setTabIndex(newIndex);
  };

  // const tabLabel = useMemo(() => {}, [tabIndex]);

  return (
    <Container sx={{ pt: 1 }}>
      <AppHeader>
        <Fab icon size="small" href="/#/home">
          <IconGenerator icon={BackIcon} />
        </Fab>
        <Typography fontWeight="bold">
          {moment(new Date()).format("DD MMM yy")}
        </Typography>
      </AppHeader>
      <Grid margin="auto" width="fit-content">
        <Tabs box={true} value={tabIndex} onChange={handleTabChange}>
          <Tab label={`${tabIndex === 0 ? " Story" : ""}`} />
          <Tab label={`${tabIndex === 1 ? " Learning" : ""}`} />
          <Tab label={`${tabIndex === 2 ? " Mistakes" : ""}`} />
        </Tabs>
      </Grid>
      <Grid className={classes.dnStoryInput}>
        <Typography component="span" variant="h5">
          Write your
          <Typography
            color="primary"
            component="span"
            variant="h5"
            fontWeight="bold"
          >
            {" story..."}
          </Typography>
        </Typography>
        <TextField
          fullWidth
          noGb
          placeholder="Write your story"
          multiline
          rows={10}
        />
      </Grid>
    </Container>
  );
}
